import React from 'react'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Button from 'react-bootstrap/Button'
import Card from 'react-bootstrap/Card'
import CardDeck from 'react-bootstrap/CardDeck'
import { Container } from 'react-bootstrap';

class ServerInfo extends React.Component {
    constructor(props){
        super(props)
    }

    render() {
        return(
            <div>
                <Jumbotron>
                    <h1>OCB Connector Server WS</h1>
                    <p>
                        This is the monitoring console of the CN server node.
                    </p>
                    <p>
                        <Button variant="primary" onClick={()=>{this.props.refreshHome()}}>Refresh</Button>
                    </p>
                </Jumbotron>
                <Container fluid>
                    <Card>
                        <Card.Header>Quick Status</Card.Header>
                        <Card.Body>
                            <CardDeck>
                                <Card style={{ minWidth: '18rem', maxWidth:'22rem', marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>VM Runtime</Card.Title>
                                        <Card.Text>
                                            {this.props.data['vmname']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>VM Vendor</Card.Title>
                                        <Card.Text>
                                            {this.props.data['vmvendor']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>Runtime version</Card.Title>
                                        <Card.Text>
                                            {this.props.data['rtversion']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>
                                
                                <Card style={{ minWidth: '18rem', maxWidth:'22rem', marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>Node name</Card.Title>
                                        <Card.Text>
                                            {this.props.data['nodename']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>Process PID</Card.Title>
                                        <Card.Text>
                                            {this.props.data['vmpid']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>DB Pool name</Card.Title>
                                        <Card.Text>
                                            {this.props.data['dbpoolname']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>DB Max lifetime</Card.Title>
                                        <Card.Text>
                                            {this.props.data['dbmaxlifetime']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>DB pool max size</Card.Title>
                                        <Card.Text>
                                            {this.props.data['dbsize']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>DB active connections</Card.Title>
                                        <Card.Text>
                                            {this.props.data['dbactive']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>DB idle connections</Card.Title>
                                        <Card.Text>
                                            {this.props.data['dbidle']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>Threads awaiting connections</Card.Title>
                                        <Card.Text>
                                            {this.props.data['dbawaiting']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>Incoming m-queue size</Card.Title>
                                        <Card.Text>
                                            {this.props.data['inQSize']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>Incoming m-queue remaing capacity</Card.Title>
                                        <Card.Text>
                                            {this.props.data['inQRemaining']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem',  marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>Incoming m-queue size</Card.Title>
                                        <Card.Text>
                                            {this.props.data['outQSize']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>

                                <Card style={{ minWidth: '18rem', maxWidth:'22rem', marginTop:'1rem'}}>
                                    <Card.Body>
                                        <Card.Title>Incoming m-queue remaing capacity</Card.Title>
                                        <Card.Text>
                                            {this.props.data['outQRemaining']}
                                        </Card.Text>
                                    </Card.Body>
                                </Card>
       
                            </CardDeck>
                            <footer className="blockquote-footer">
                                SAP SE <cite title="Source Title">FSI</cite>
                            </footer>
                        </Card.Body>
                    </Card>
                </Container>
            </div>
        );
    }
}

export default ServerInfo;