import React from 'react'
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav'
import NavDropdown from 'react-bootstrap/NavDropdown'
import Spinner from './Spinner.js'
class Header extends React.Component {
    constructor(props){
        super(props)
    }
    render() {
        return(
            <Navbar bg="dark" expand="lg" variant="dark">
               <Navbar.Brand href="#home">CN Control Panel</Navbar.Brand>
               <Navbar.Toggle aria-controls="basic-navbar-nav" />
               <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                    <Nav.Link onClick={()=>this.props.homeClick()}>Home</Nav.Link>
                    <NavDropdown title="Actions" id="basic-nav-dropdown">
                        <NavDropdown.Item onClick={()=>this.props.avaliableEvents()}>Available Events</NavDropdown.Item>
                        <NavDropdown.Item onClick={()=>this.props.transactionHistoryClick()}>Transaction history</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item onClick={()=>this.props.logoutClick()}>Logout</NavDropdown.Item>
                    </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
                <Spinner showloader={this.props.showloader}/>
            </Navbar>
        );
    }
}

export default Header