import './App.css';
import React from 'react'
import DataView from './components/DataView.js'
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './components/Header.js'
import axios from 'axios';

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {transactions:[],
                  screen:'home',
                  availableEvents:[],
                  serverinfo:[],
                  showloader:true};
  }

  componentDidMount() {
    this.homeClick();
  }

  render() {
    console.log(this.state)
    return (
      <div>
        <Header 
        avaliableEvents={()=>this.avaliableEventsHandleClick()} 
        transactionHistoryClick={()=>this.transactionHistoryClick()}
        homeClick={()=>{this.homeClick()}}
        logoutClick={()=>{this.logoutClick()}}
        showloader={this.state.showloader}/>
      
        <DataView data={this.state} 
        txn={this.state.transactions}
        events={this.state.availableEvents}
        mode={this.state.screen}
        refreshHome={()=>{this.homeClick()}}/>

      </div>
      );
  }

  avaliableEventsHandleClick() {
    this.setState({...this.state,showloader:true})
    this.getAvailableEvents();
    this.setState({...this.state,screen:'events',showloader:true})
  }

  homeClick() {
    this.setState({...this.state,showloader:true})
    this.getServerInfo();
    this.setState({...this.state,screen:'home',showloader:true})
  }

  transactionHistoryClick() {
    this.setState({...this.state,showloader:true})
    this.getTransactionHistory();
    this.setState({...this.state,screen:'txns',showloader:true})
  }

  logoutClick() {
    this.logout();
  }


  logout() {
    axios.get('/console_wp/logout')
    .then(res => {
      window.open("/console");
    })
  }

  getTransactionHistory() {
    axios.get('/console_wp/txnStatus')
    .then(res => {
      const transactions = res.data;
      this.setState({...this.state,transactions:transactions,showloader:false});
    })
  }

  getAvailableEvents() {
    axios.get('/console_wp/availableEvents')
    .then(res => {
      const events = res.data;
      this.setState({...this.state,availableEvents:events,showloader:false});
    })
  }

  getServerInfo() {
    axios.get('/console_wp/serverInfo')
    .then(res => {
      const info = res.data;
      this.setState({...this.state,serverinfo:info,showloader:false});
    })
  }

}

export default App;
