import React from 'react'
import { Container } from 'react-bootstrap';
import TransactionTable from './TransactionTable.js';
import Table from 'react-bootstrap/Table';
import ServerInfo from './ServerInfo.js'

class DataView extends React.Component {
constructor(props) {
    super(props);
}

    render() {
        if(this.props.mode == 'txns') {
            return(<TransactionTable data={this.props.txn}/>);

            /*let rows = [];
            let i = 1;
            for (let txnkey of Object.keys(this.props.txn)) {
                rows.push(this.getTxnObject(this.props.txn[txnkey],i));
                i++;
            }
            if(rows.length==0) {
                rows.push(<tr key='no_data'><td colSpan={5}><center>No data</center></td></tr>)
            }
            
            return(
                <Container fluid>
                    <Table striped bordered hover>
                        <thead>
                            <tr key='title_1'>
                            <th>#</th>
                            <th>Transaction ID</th>
                            <th>Transaction Status</th>
                            <th>Data</th>
                            <th>Timing</th>
                            </tr>
                        </thead>
                        <tbody>
                            {rows}
                        </tbody>
                    </Table>
                </Container>
            );*/
        } else if(this.props.mode == 'events'){
            let rows = [];
            let i = 1;
            var cdr = this.props.events.sort();
            for (let entry of cdr) {
                rows.push(this.getEventObject(entry,i));
                i++;
            }
            return(
            <Container fluid>
                <Table striped bordered hover>
                    <thead>
                        <tr key='title_2'>
                            <th>#</th>
                            <th>Event Module</th>
                            <th>Event Action name</th>
                            <th>Target class</th>
                            <th>Arguments</th>
                        </tr>
                    </thead>
                    <tbody>
                        {rows}
                    </tbody>
                </Table>
            </Container>);
        } else if(this.props.mode == 'home') {
            return <ServerInfo refreshHome={()=>{this.props.refreshHome()}} data={this.props.data.serverinfo}/>
        } else {
            return(<h1>unexpected</h1>)
        }
    }

    getTxnObject(txmObject,count) {
        return(<tr key={txmObject.txnId}>
            <td>{count}</td>
            <td>
                {txmObject.txnId}
            </td>
            <td>
                {txmObject.status}
            </td>
            <td>
                <ul>
                    <li>
                        Module Name : {txmObject.remoteActionMessageBean.moduleName} 
                    </li>
                    <li>
                        Action : {txmObject.remoteActionMessageBean.actionName}
                    </li>
                    <li>
                        Connection ID : {txmObject.remoteActionMessageBean.connectionId} 
                    </li>
                    <li>
                        Incoming Message : {JSON.stringify(txmObject.inbound)} 
                    </li>
                    <li>
                        Return Message : {JSON.stringify(txmObject.remoteActionMessageBean.message)} 
                    </li>
                    <li>
                        Sync Txn : {txmObject.remoteActionMessageBean.sync.toString()}
                    </li>
                    <li>
                        User ID : {txmObject.remoteActionMessageBean.userId} 
                    </li>
                    <li>
                       Message Fate : {txmObject.remoteActionMessageBean.status} 
                    </li>
                </ul>
            </td>
            <td>
                <ul>
                    <li>
                        Start time : {txmObject.recievedTime} 
                    </li>
                    <li>
                        End time : {txmObject.processingCompleteTime}
                    </li>
                    <li>
                        Processing duration : {txmObject.timeTookForProcessing} 
                    </li>
                </ul>
            </td>
        </tr>);
    }

    getEventObject(eventobj,count) {
      
        return(
        <tr key={JSON.stringify(eventobj)}>
                {console.log(eventobj)}
                <td>{count}</td>
                <td>
                    <i>
                    {eventobj.split(':')[0]}
                    </i>
                </td>
                <td>
                    <i>
                    {eventobj.split(':')[1]}
                    </i>
                </td>
                <td>
                    {eventobj.split(':')[2]}
                </td>
                <td>
                    {this.inserLineBreak(eventobj.split(':')[3])}
                </td>
            </tr>);
    }

    inserLineBreak(methods) {
        let rows = [];
        for(let md of methods
            .replaceAll('class','')
            .replace('[','')
            .replace(']','')
            .split(',')) {
            rows.push(<i>{md}</i>)
            rows.push(<br/>)
        }
        return(rows);
    }
}

export default DataView